<template>
    <div class="login-container">
      <div class="content-wrapper">
        <div class="image-container">
          <img src="@/assets/images/cover-light.webp" alt="Cover" class="cover-image">
        </div>
        <h1 class="title">The Rosary Novena</h1>
        <div class="buttons-container">
          <button class="ghost-button" @click="goToCredentials('signin')">Sign In</button>
          <button class="ghost-button" @click="goToCredentials('signup')">Create New Account</button>
        </div>
      </div>
    </div>
</template>

<script>
import { userStore } from '@/store/user'

export default {
  name: 'LoginPage',
  created() {
  // Check if user is already logged in using localStorage
  if (userStore.isAuthenticated()) {
    this.$router.replace({ name: 'Welcome', params: { mode: 'signin' } })
  }
},
  methods: {
    goToCredentials(mode) {
      this.$router.push({
        name: 'Credentials',
        params: { mode: mode }
      })
    }
  }
}
</script>

