<template>
  <div class="settings-container">
    <div class="content-wrapper">
      <h1 class="title">Settings</h1>

      <!-- Flame of Love Toggle -->
      <div class="setting-section">
        <label class="setting-label">Flame of Love</label>
        <div class="toggle-container">
          <label class="toggle-switch">
            <input type="checkbox" v-model="localFlameOfLove" @change="updateFlameOfLove">
            <span class="slider"></span>
          </label>
          <span class="toggle-text">{{ localFlameOfLove ? 'On' : 'Off' }}</span>
        </div>
      </div>

      <!-- Show Password -->
      <div class="setting-section">
        <label class="setting-label">Current Password</label>
        <div class="password-display">
          <span>{{ showPassword ? user.password : '' }}</span>
          <button @click="toggleShowPassword" class="ghost-button small">
            {{ showPassword ? 'Hide' : 'Show' }}
          </button>
        </div>
      </div>

      <!-- Change Password -->
      <div class="setting-section">
        <label class="setting-label">New Password</label>
        <input 
          type="text" 
          v-model="newPassword" 
          class="input-field"
          placeholder="Enter new password"
        />
        <button 
          @click="updatePassword" 
          class="ghost-button"
          :disabled="!newPassword || updating"
        >
          {{ updating ? 'Updating...' : 'Change Password' }}
        </button>
      </div>

      <!-- Delete Account Section -->
      <div class="setting-section danger-zone">
        <label class="setting-label danger-label">Danger Zone</label>
        <p class="danger-text">Deleting your account will permanently remove all your data including your rosary count and novena progress. This action cannot be undone.</p>
        <button 
          @click="showDeleteConfirmation = true" 
          class="ghost-button danger-button"
        >
          Delete Account
        </button>
      </div>

      <!-- Exit Settings -->
      <div class="buttons-container">
        <button @click="exitSettings" class="ghost-button">
          Exit Settings
        </button>
      </div>
    </div>

    <!-- Delete Confirmation Modal -->
    <div v-if="showDeleteConfirmation" class="modal-overlay" @click="showDeleteConfirmation = false">
      <div class="modal-content" @click.stop>
        <h2 class="modal-title">Delete Account?</h2>
        <p class="modal-text">Are you absolutely sure? This will permanently delete:</p>
        <ul class="modal-list">
          <li>Your account information</li>
          <li>All rosary and novena progress</li>
          <li>Your prayer intentions</li>
        </ul>
        <p class="modal-text"><strong>This action cannot be undone.</strong></p>
        <div class="modal-buttons">
          <button @click="deleteAccount" class="ghost-button danger-button">
            Yes, Delete My Account
          </button>
          <button @click="showDeleteConfirmation = false" class="ghost-button">
            Cancel
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userStore } from '@/store/user'
import { API_URL } from '@/config'

export default {
  name: 'SettingsPage',
  data() {
    return {
      localFlameOfLove: false,
      showPassword: false,
      newPassword: '',
      updating: false,
      showDeleteConfirmation: false
    }
  },
  computed: {
    user() {
      return userStore.currentUser.value || {}
    }
  },
  created() {
    if (!this.user || !this.user.firstName) {
      this.$router.push('/')
      return
    }
    this.localFlameOfLove = this.user.flameOfLove
  },
  methods: {
    toggleShowPassword() {
      this.showPassword = !this.showPassword
    },
    
    async updateFlameOfLove() {
      try {
        const params = new URLSearchParams({
          path: 'update-user',
          firstName: this.user.firstName,
          middleName: this.user.middleName,
          lastName: this.user.lastName,
          password: this.user.password,
          intention: this.user.intention,
          day: this.user.day,
          dateLastCompleted: this.user.dateLastCompleted,
          total: this.user.total,
          petition: this.user.petition,
          thanksgiving: this.user.thanksgiving,
          flameOfLove: this.localFlameOfLove
        })

        const response = await fetch(`${API_URL}?${params.toString()}`)
        const data = await response.json()

        if (data.success) {
          userStore.setUser(data.user)
          alert('Flame of Love setting updated!')
        } else {
          throw new Error(data.message)
        }
      } catch (error) {
        console.error('Error updating Flame of Love:', error)
        alert('Failed to update setting. Please try again.')
        this.localFlameOfLove = this.user.flameOfLove
      }
    },

    async updatePassword() {
      if (!this.newPassword) {
        alert('Please enter a new password')
        return
      }

      this.updating = true

      try {
        const params = new URLSearchParams({
          path: 'update-user',
          firstName: this.user.firstName,
          middleName: this.user.middleName,
          lastName: this.user.lastName,
          password: this.user.password,
          newPassword: this.newPassword,
          intention: this.user.intention,
          day: this.user.day,
          dateLastCompleted: this.user.dateLastCompleted,
          total: this.user.total,
          petition: this.user.petition,
          thanksgiving: this.user.thanksgiving,
          flameOfLove: this.user.flameOfLove
        })

        const response = await fetch(`${API_URL}?${params.toString()}`)
        const data = await response.json()

        if (data.success) {
          userStore.setUser(data.user)
          alert('Password updated successfully!')
          this.newPassword = ''
          this.showPassword = false
        } else {
          throw new Error(data.message)
        }
      } catch (error) {
        console.error('Error updating password:', error)
        alert('Failed to update password. Please try again.')
      } finally {
        this.updating = false
      }
    },

    async deleteAccount() {
      try {
        const params = new URLSearchParams({
          path: 'delete-user',
          firstName: this.user.firstName,
          middleName: this.user.middleName,
          lastName: this.user.lastName,
          password: this.user.password
        })

        const response = await fetch(`${API_URL}?${params.toString()}`)
        const data = await response.json()

        if (data.success) {
          // Clear user from store and localStorage
          await userStore.clearUser()
          // Redirect to login
          this.$router.push('/')
        } else {
          throw new Error(data.message)
        }
      } catch (error) {
        console.error('Error deleting account:', error)
        alert('Failed to delete account. Please try again.')
      }
    },

    exitSettings() {
      this.$router.push({ name: 'Welcome' })
    }
  }
}
</script>

