<template>
    <div class="novena-start-container">
        <div class="content-wrapper">
            <!-- Previous content remains the same -->
            <img :src="mysteryImage" :alt="`${mysteryType} Mysteries`" class="mystery-cover-image">

            <h1 class="title">{{ mysteryType }} Mysteries</h1>

            <div class="novena-details">
                <p class="day-number">Day {{ user.day }}</p>
                <p class="intention" :class="{ 'thanksgiving': user.thanksgiving }">
                    {{ intentionText }}
                </p>
            </div>

            <!-- First Prayer: Sign of the Cross -->
            <div v-if="currentPrayer === 1" class="prayer-container">

                <div class="audio-controls">
                    <audio ref="audioPlayerCross" :src="require('@/assets/audio/sign-of-cross.mp3')" controls></audio>
                </div>

                <p class="prayer-text">
                    In the name of the Father, and of the Son, and of the Holy Spirit. Amen.
                </p>

                <img src="@/assets/images/sign-of-the-cross.png" alt="Sign of the Cross" class="prayer-image">
            </div>

            <!-- Second Prayer: Hail Mary -->
            <div v-else-if="currentPrayer === 2" class="prayer-container">
                <audio ref="audioPlayerHailMary" :src="prayerAudio" controls></audio>

                <p class="prayer-text" v-html="prayerText"></p>

                <img src="@/assets/images/rosary/1.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Third Prayer: Petition or Thanksgiving -->
            <div v-else-if="currentPrayer === 3" class="prayer-container">
                <audio ref="audioPlayerThirdPrayer" :src="thirdPrayerAudio" controls></audio>

                <p class="prayer-text">
                    {{ thirdPrayerText }}
                </p>

                <img src="@/assets/images/rosary/1.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Fourth Prayer: Creed -->
            <div v-else-if="currentPrayer === 4" class="prayer-container">
                <div class="audio-controls">
                    <audio ref="audioPlayerCreed" :src="require('@/assets/audio/apostles-creed.mp3')" controls></audio>
                </div>

                <p class="prayer-text">
                    I believe in God, the Father almighty, creator of heaven and earth. I believe in Jesus Christ, his
                    only Son, our Lord, who was conceived by the Holy Spirit and born of the virgin Mary. He suffered
                    under Pontius Pilate, was crucified, died, and was buried; he descended to hell. The third day he
                    rose again from the dead. He ascended to heaven and is seated at the right hand of God the Father
                    almighty. From there he will come to judge the living and the dead. <strong>I believe in the Holy Spirit,
                    the holy Catholic Church, the communion of saints, the forgiveness of sins, the resurrection of the
                    body, and the life everlasting. Amen.</strong></p>

                <img src="@/assets/images/rosary/1.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Fifth Prayer: Our Father -->
            <div v-else-if="currentPrayer === 5" class="prayer-container">
                <div class="audio-controls">
                    <audio ref="audioPlayerOurFather" :src="require('@/assets/audio/our-father.mp3')" controls></audio>
                </div>

                <p class="prayer-text">
                    Our Father, Who art in heaven, Hallowed be Thy Name. Thy Kingdom come. Thy Will be done, on earth as it is in Heaven. <strong>Give us this day our daily bread. And forgive us our trespasses, as we forgive those who trespass against us. And lead us not into temptation, but deliver us from evil. Amen</strong></p>

                <img src="@/assets/images/rosary/2.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Sixth Prayer: 3 Hail Marys -->
            <div v-else-if="currentPrayer === 6" class="prayer-container">
                <audio ref="audioPlayerHailMary6" :src="prayerAudio" controls></audio>

                <h5 class="hail-mary-counter">{{ hailMaryCount }} of 3</h5>
                <p class="prayer-text" style="white-space: pre-line;" v-html="prayerText"></p>

                <img :src="require(`@/assets/images/rosary/${hailMaryCount + 2}.png`)" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Seventh Prayer: Glory Be -->
            <div v-else-if="currentPrayer === 7" class="prayer-container">
                <div class="audio-controls">
                    <audio ref="audioPlayerGloryBe" :src="require('@/assets/audio/glory-be.mp3')" controls></audio>
                </div>

                <p class="prayer-text">
                    Glory be to the Father, and to the Son, and to the Holy Spirit. <strong>As it was in the beginning, is now, and ever shall be, world without end. Amen.</strong>
                </p>

                <img src="@/assets/images/rosary/6.png" alt="Rosary Prayer" class="prayer-image">
            </div>

             <!-- Continue -->
             <div class="buttons-container">
                    <button @click="continueNovena" class="ghost-button">
                        Continue
                    </button>
                    <button v-if="canGoBack" @click="goBack" class="ghost-button back-button">
                        Back
                    </button>
            </div>

            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
    import {
        userStore
    } from '@/store/user'
    export default {
        name: 'NovenaStartPage',
        data() {
            return {
                mysteryTypes: {
                    joyful: [1, 4, 7, 10, 13, 16, 19, 22, 25],
                    sorrowful: [2, 5, 8, 11, 14, 17, 20, 23, 26],
                    glorious: [3, 6, 9, 12, 15, 18, 21, 24, 27]
                },
                canContinue: true,
                currentPrayer: 1, // Starting at the first prayer
                hailMaryCount: 1  // Counter for Hail Mary prayers in prayer 6
            }
        },
        computed: {
            user() {
                return userStore.currentUser.value || {}
            },
            prayerAudio() {
                console.log('Full user object:', this.user)
                console.log('Flame of Love (type):', typeof this.user.flameOfLove)
                console.log('Flame of Love (value):', this.user.flameOfLove)
                return this.user.flameOfLove ?
                    require('@/assets/audio/hail-mary-flame.mp3') :
                    require('@/assets/audio/hail-mary.mp3')
            },
            prayerText() {
                console.log('Full user object:', this.user)
                console.log('Flame of Love (type):', typeof this.user.flameOfLove)
                console.log('Flame of Love (value):', this.user.flameOfLove)
                const baseText =
                    'Hail Mary, full of grace, the Lord is with Thee, blessed are Thou among women, and blessed is the fruit of Thy womb, Jesus. <strong>Holy Mary, Mother of God, pray for us sinners, '
                return this.user.flameOfLove ?
                    `${baseText}spread the effect of grace of Thy Flame of Love over all of humanity, now and at the hour of our death. Amen.</strong>` :
                    `${baseText}now and at the hour of our death. Amen.</strong>`
            },
            mysteryType() {
                if (!this.user) return 'Unknown'
                const day = (this.user.day || 0)
                for (const [type, days] of Object.entries(this.mysteryTypes)) {
                    if (days.includes(day)) {
                        return type.charAt(0).toUpperCase() + type.slice(1)
                    }
                }
                return 'Unknown'
            },
            mysteryImage() {
                try {
                    const lowercaseMysteryType = this.mysteryType.toLowerCase()
                    return require(`@/assets/images/${lowercaseMysteryType}/cover.webp`)
                } catch (error) {
                    console.error('Error loading mystery image:', error)
                    return '' // or a default image path
                }
            },
            intentionText() {
                if (!this.user) return ''
                return this.user.thanksgiving ?
                    `In thanksgiving for ${this.user.intention || ''}` :
                    `In petition for ${this.user.intention || ''}`
            },
            thirdPrayerText() {
                const prayers = {
                    Joyful: {
                        petition: `Hail, Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I humbly kneel to offer thee a Crown of Roses  snow-white buds to remind thee of thy joys  each bud recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my petition; from thy bounty thou wilt give me the favor I so earnestly and trustingly seek.\n\nI despair of nothing that I ask of thee. Show thyself my Mother!`,
                        thanksgiving: `Hail, Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I gratefully kneel to offer thee a Crown of Roses  snow-white buds to remind thee of thy joys  each bud recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my thanksgiving; from thy bounty thou hast given me the favor I so earnestly and trustingly sought.\n\nI despaired not of what I asked of thee, and thou hast truly shown thyself my Mother.`
                    },
                    Sorrowful: {
                        petition: `Hail, Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I humbly kneel to offer thee a Crown of Roses  blood-red roses to remind thee of the passion of thy divine Son, with Whom thou didst so fully partake of its bitterness  each rose recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my petition; from thy bounty thou wilt give me the favor I so earnestly and trustingly seek.\n\nI despair of nothing that I ask of thee. Show thyself my Mother!`,
                        thanksgiving: `Hail, Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I gratefully kneel to offer thee a Crown of Roses-blood red roses to remind thee of the passion of thy divine Son, with Whom thou didst so fully partake of its bitterness  each rose recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my thanksgiving; from thy bounty thou hast given me the favor I so earnestly and trustingly sought.\n\nI despaired not of what I asked of thee, and thou hast truly shown thyself my Mother.`
                    },
                    Glorious: {
                        petition: `Hail, Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I humbly kneel to offer thee a Crown of Roses  full-blown white roses, tinged with the red of the passion, to remind thee of thy glories, fruits of the sufferings of thy Son and thee  each rose recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my petition; from thy bounty thou wilt give me the favor I so earnestly and trustingly seek.\n\nI despair of nothing that I ask of thee. Show thyself my Mother!`,
                        thanksgiving: `Hail! Queen of the Most Holy Rosary, my Mother Mary, hail! At thy feet I gratefully kneel to offer thee a Crown of Roses-full blown white roses, tinged with the red of the passion, to remind thee of thy glories, fruits of the sufferings of thy Son and thee-each rose recalling to thee a holy mystery; each ten bound together with my petition for a particular grace.\n\nO Holy Queen, dispenser of God's graces, and Mother of all who invoke thee! Thou canst not look upon my gift and fail to see its binding. As thou receivest my gift, so wilt thou receive my thanksgiving; from thy bounty thou hast given me the favor I so earnestly and trustingly sought.\n\nI despaired not of what I asked of thee, and thou hast truly shown thyself my Mother.`
                    }
                };
                const prayerType = this.user.thanksgiving ? 'thanksgiving' : 'petition';
                return prayers[this.mysteryType]?.[prayerType] || '';
            },
            thirdPrayerAudio() {
                const mysteryLower = this.mysteryType.toLowerCase();
                const prayerType = this.user.thanksgiving ? 'thanksgiving' : 'petition';
                try {
                    return require(`@/assets/audio/${mysteryLower}/${mysteryLower}-${prayerType}.mp3`);
                } catch (error) {
                    console.error('Error loading prayer audio:', error);
                    return '';
                }
            },
            canGoBack() {
                return true
            }
        },
        created() {
            // Ensure user data is loaded
            if (!this.user || !this.user.day) {
                this.$router.push('/')
            }
        },
        methods: {
            playAudio(type) {
                if (type === 'cross') {
                    this.$refs.audioPlayerCross.play()
                } else if (type === 'hailMary') {
                    this.$refs.audioPlayerHailMary.play()
                } else if (type === 'thirdPrayer') {
                    this.$refs.audioPlayerThirdPrayer.play()
                } else if (type === 'creed') {
                    this.$refs.audioPlayerCreed.play()
                } else if (type === 'ourFather') {
                    this.$refs.audioPlayerOurFather.play()
                } else if (type === 'hailMary6') {
                    this.$refs.audioPlayerHailMary6.play();
                } else if (type === 'gloryBe') {
                    this.$refs.audioPlayerGloryBe.play();
                }
            },
            continueNovena() {
                window.scrollTo(0, 0);  // Scroll to top

                // Special handling for prayer 6 (3 Hail Marys)
                if (this.currentPrayer === 6 && this.hailMaryCount < 3) {
                    // Increment Hail Mary counter
                    this.hailMaryCount++;
                    
                    // Reset the audio player to beginning
                    const audioPlayer = this.$refs.audioPlayerHailMary6;
                    if (audioPlayer) {
                        // Pause if playing
                        if (!audioPlayer.paused) {
                            audioPlayer.pause();
                        }
                        // Reset to beginning
                        audioPlayer.currentTime = 0;
                    }
                    
                    // Reset continue button
                    return;
                }
                
                // Normal flow for other prayers
                this.currentPrayer++;
                // Reset Hail Mary counter if we're moving to a different prayer
                if (this.currentPrayer !== 6) {
                    this.hailMaryCount = 1;
                }
                
                // Logic to handle different prayers
                if (this.currentPrayer <= 7) {
                    console.log(`Moving to prayer ${this.currentPrayer}`);
                } else {
                    // All prayers completed, move to next stage
                    this.$router.push({
                        name: 'NovenaDay',
                        params: {
                            day: this.user.day
                        }
                    });
                }
            },
            goBack() {
                window.scrollTo(0, 0);  // Scroll to top

                // Special handling for 3 Hail Marys
                if (this.currentPrayer === 6 && this.hailMaryCount > 1) {
                    this.hailMaryCount--
                    
                    // Reset audio
                    const audioPlayer = this.$refs.audioPlayerHailMary6
                    if (audioPlayer) {
                        if (!audioPlayer.paused) {
                            audioPlayer.pause()
                        }
                        audioPlayer.currentTime = 0
                    }
                    return
                }
                
                // Go to previous prayer
                if (this.currentPrayer > 1) {
                    this.currentPrayer--
                    
                    // If going back to prayer 6, set hailMaryCount to 3
                    if (this.currentPrayer === 6) {
                        this.hailMaryCount = 3
                    }
                } else {
                    // On first prayer, go back to Welcome
                    this.$router.push({ name: 'Welcome' })
                }
            },
            async signOut() {
                await userStore.clearUser()
                this.$router.push('/')
            }
        }
    }
</script>

