<template>
    <div class="warning-container">
        <div class="content-wrapper">
            <h1 class="title">Caution Before Starting!</h1>
            <p class="warning-text">
                Starting a new novena will erase your current novena progress.
            </p>
            <p class="warning-text">
                Would you like to continue?
            </p>
            <div class="buttons-container">
                <button @click="confirmNewNovena" class="ghost-button">
                    Yes
                </button>
                <button @click="returnToWelcome" class="ghost-button">
                    No
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        userStore
    } from '@/store/user'
    import {
        API_URL
    } from '@/config'
    export default {
        name: 'NovenaWarningPage',
        methods: {
            async confirmNewNovena() {
                try {
                    // Preserve user's identifying information
                    const userData = {
                        firstName: this.user.firstName,
                        middleName: this.user.middleName,
                        lastName: this.user.lastName,
                        password: this.user.password,
                        intention: "",
                        day: 0,
                        dateLastCompleted: "",
                        total: this.user.total || 0,
                        petition: true,
                        thanksgiving: false,
                        flameOfLove: true
                    }
                    
                    // Changed to GET request with all parameters in URL
                    const params = new URLSearchParams({
                        path: 'update-user',
                        ...userData
                    });
                    
                    const response = await fetch(`${API_URL}?${params.toString()}`, {
                        method: 'GET'
                    });
                    
                    const data = await response.json();
                    if (data.success) {
                        userStore.setUser(userData)
                        this.$router.push({
                            name: 'ChooseIntentionType',
                            params: {
                                userId: `${userData.firstName}-${userData.lastName}`.toLowerCase()
                            }
                        })
                    }
                } catch (error) {
                    console.error('Error resetting novena:', error)
                }
            },
            returnToWelcome() {
                this.$router.push({
                    name: 'Welcome'
                })
            }
        },
        computed: {
            user() {
                return userStore.currentUser.value
            }
        }
    }
</script>

