<template>
    <div class="novena-end-container">
        <div class="content-wrapper">
            <!-- Mystery Cover Image -->
            <img :src="mysteryImage" :alt="`${mysteryType} Mysteries`" class="mystery-cover-image">

            <h1 class="title">{{ mysteryType }} Mysteries</h1>

            <div class="novena-details">
                <p class="day-number">Day {{ user.day }}</p>
                <p class="intention" :class="{ 'thanksgiving': user.thanksgiving }">
                    {{ intentionText }}
                </p>
            </div>

            <!-- Prayer 1: Hail Holy Queen -->
            <div v-if="currentPrayer === 1" class="prayer-container">
                <audio ref="audioPlayerHailHolyQueen" :src="require('@/assets/audio/hail-holy-queen.mp3')" controls></audio>

                <p class="prayer-text">
                    Hail, holy queen, mother of mercy, our life, our sweetness, and our hope! To thee do we cry, poor banished children of Eve, to thee do we send up our sighs, mourning and weeping in this valley of tears. Turn then, most gracious advocate, thine eyes of mercy toward us; and after this our exile show unto us the blessed fruit of thy womb, Jesus. O clement, O loving, O sweet Virgin Mary!
                </p>
                <p class="prayer-text">
                    <strong>P.</strong> Pray for us, O holy mother of God.<br>
                    <strong>R.</strong> That we may be made worthy of the promises of Christ.
                </p>
                <p class="prayer-text">
                    Let us pray.
                </p>

                <img src="@/assets/images/rosary/61.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 2: Spiritual Communion -->
            <div v-else-if="currentPrayer === 2" class="prayer-container">
                <audio ref="audioPlayerSpiritualCommunion" :src="spiritualCommunionAudio" controls></audio>

                <p class="prayer-text" style="white-space: pre-line;" v-html="spiritualCommunionText"></p>

                <img src="@/assets/images/rosary/61.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 3: Hail Mary -->
            <div v-else-if="currentPrayer === 3" class="prayer-container">
                <audio ref="audioPlayerHailMary" :src="prayerAudio" controls></audio>

                <p class="prayer-text" style="white-space: pre-line;" v-html="prayerText"></p>

                <img src="@/assets/images/rosary/61.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 4: Closing Prayer -->
            <div v-else-if="currentPrayer === 4" class="prayer-container">
                <audio ref="audioPlayerClosing" :src="closingPrayerAudio" controls></audio>

                <p class="prayer-text" style="white-space: pre-line;">
                    {{ closingPrayerText }}
                </p>

                <img src="@/assets/images/rosary/61.png" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 5: Sign of the Cross -->
            <div v-else-if="currentPrayer === 5" class="prayer-container">
                <audio ref="audioPlayerCrossEnd" :src="require('@/assets/audio/sign-of-cross.mp3')" controls></audio>

                <p class="prayer-text">
                    In the name of the Father, and of the Son, and of the Holy Spirit. Amen.
                </p>

                <img src="@/assets/images/sign-of-the-cross.png" alt="Sign of the Cross" class="prayer-image">
            </div>

            <!-- Continue -->
            <div class="buttons-container">
                <button v-if="currentPrayer < 5" @click="continueNovena" class="ghost-button">
                    Continue
                </button>
                <button v-else @click="finishNovena" class="ghost-button">
                    Finish
                </button>
                <button v-if="canGoBack" @click="goBack" class="ghost-button back-button">
                    Back
                </button>
            </div>

            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
import { userStore } from '@/store/user'
import confetti from 'canvas-confetti'

export default {
    name: 'NovenaEnd',
    data() {
        return {
            mysteryTypes: {
                joyful: [1, 4, 7, 10, 13, 16, 19, 22, 25],
                sorrowful: [2, 5, 8, 11, 14, 17, 20, 23, 26],
                glorious: [3, 6, 9, 12, 15, 18, 21, 24, 27]
            },
            currentPrayer: 1
        }
    },
    computed: {
        user() {
            return userStore.currentUser.value || {}
        },
        mysteryType() {
            if (!this.user) return 'Unknown'
            const day = (this.user.day || 0)
            for (const [type, days] of Object.entries(this.mysteryTypes)) {
                if (days.includes(day)) {
                    return type.charAt(0).toUpperCase() + type.slice(1)
                }
            }
            return 'Unknown'
        },
        mysteryImage() {
            try {
                const lowercaseMysteryType = this.mysteryType.toLowerCase()
                return require(`@/assets/images/${lowercaseMysteryType}/cover.webp`)
            } catch (error) {
                console.error('Error loading mystery image:', error)
                return ''
            }
        },
        intentionText() {
            if (!this.user) return ''
            return this.user.thanksgiving ?
                `In thanksgiving for ${this.user.intention || ''}` :
                `In petition for ${this.user.intention || ''}`
        },
        spiritualCommunionAudio() {
            const prayerType = this.user.thanksgiving ? 'thanksgiving' : 'petition'
            try {
                return require(`@/assets/audio/spiritual-communion-${prayerType}.mp3`)
            } catch (error) {
                console.error('Error loading spiritual communion audio:', error)
                return ''
            }
        },
        spiritualCommunionText() {
            const mainText = `My Jesus, really present in the most holy Sacrament of the Altar, since I cannot now receive Thee under the sacramental veil, I beseech Thee, with a heart full of love and longing, to come spiritually into my soul through the immaculate heart of Thy most holy Mother, and abide with me forever; Thou in me, and I in Thee, in time and in eternity.\n\n`;
            
            if (this.user.thanksgiving) {
                return `${mainText}Sweet Mother Mary, I offer thee this Spiritual Communion to bind my bouquets in a wreath to place upon thy brow in thanksgiving for <em>${this.user.intention}</em>, which thou in thy love hast obtained for me.`;
            } else {
                return `${mainText}Sweet Mother Mary, I offer thee this Spiritual Communion to bind my bouquets in a wreath to place upon thy brow.\n\nO my Mother! Look with favor upon my gift, and in thy love obtain for me <em>${this.user.intention}</em>.`;
            }
        },
        isDaytime() {
            const hour = new Date().getHours()
            // Consider daytime from 6 AM to 9 PM (6-21)
            return hour >= 6 && hour < 21
        },
        closingPrayerAudio() {
            const timeOfDay = this.isDaytime ? 'day' : 'night'
            try {
                return require(`@/assets/audio/closing-prayer-${timeOfDay}.mp3`)
            } catch (error) {
                console.error('Error loading closing prayer audio:', error)
                return ''
            }
        },
        closingPrayerText() {
            const timeOfDay = this.isDaytime ? 'day' : 'night';
            return `O God! Whose only-begotten Son, by His life, death, and resurrection, hast purchased for the reward of eternal life; grant, we beseech Thee, that, meditating upon these mysteries of the Most Holy Rosary of the Blessed Virgin Mary, we may imitate what they contain and obtain what they promise. Through the same Christ our Lord. Amen.\n\nMay the divine assistance remain always with us. Amen. And may the souls of the faithful departed, through the mercy of God, rest in peace. Amen.\n\nHoly Virgin, with thy loving Child, thy blessing give to us this ${timeOfDay}.`;
        },
        prayerAudio() {
            return this.user.flameOfLove ?
            require('@/assets/audio/hail-mary-flame.mp3') :
            require('@/assets/audio/hail-mary.mp3')
        }, 
        prayerText() {
            const baseText = 'Hail Mary, full of grace, the Lord is with Thee, blessed are Thou among women, and blessed is the fruit of Thy womb, Jesus. <strong>Holy Mary, Mother of God, pray for us sinners, '
            return this.user.flameOfLove ?
                `${baseText}spread the effect of grace of Thy Flame of Love over all of humanity, now and at the hour of our death. Amen.</strong>` :
                `${baseText}now and at the hour of our death. Amen.</strong>`
        },
        canGoBack() {
            return true
        }
    },
    created() {
        if (!this.user || !this.user.day) {
            this.$router.push('/')
        }
    },
    methods: {
        continueNovena() {
            window.scrollTo(0, 0)
            this.currentPrayer++
        },
        goBack() {
            window.scrollTo(0, 0)
            
            if (this.currentPrayer > 1) {
                this.currentPrayer--
            } else {
                // Go back to NovenaDay (last decade, last prayer)
                this.$router.push({
                    name: 'NovenaDay',
                    params: {
                        day: this.user.day
                    }
                })
            }
        },
        async finishNovena() {
            // Fire confetti!
            confetti({
                particleCount: 150,
                spread: 70,
                origin: { y: 0.6 },
                colors: ['#707070', '#f5f5f5', '#e0e0e0', '#ffffff']  // Gray and off-white
            });
            
            // Increment the user's day
            const newDay = this.user.day + 1
            
            // Update the user's day in the store
            await userStore.updateUserDay(newDay)
            
            // Navigate to Welcome page
            this.$router.push({
                name: 'Welcome'
            })
        },
        async signOut() {
            await userStore.clearUser()
            this.$router.push('/')
        }
    }
}
</script>

