<template>
  <div class="existing-intentions-container">
    <div class="content-wrapper">
      <h1 class="title">Existing Intentions</h1>
      
      <p class="subtitle">Select an existing intention from the list to pray for.</p>
      
      <div class="search-container">
        <input 
          type="text" 
          v-model="searchQuery" 
          placeholder="Search intentions..." 
          class="search-input"
        />
      </div>
      
      <div class="intentions-list-container">
        <div 
          v-if="filteredIntentions.length === 0" 
          class="no-intentions"
        >
          No intentions found.
        </div>
        
        <div 
          v-else 
          class="intentions-list"
        >
          <label 
            v-for="intention in filteredIntentions" 
            :key="intention.id" 
            class="intention-item"
          >
            <input 
              type="radio" 
              :value="intention" 
              v-model="selectedIntention" 
              class="intention-radio"
            />
            <span class="intention-text">
              {{ intention.text }} 
              <span class="intention-author">
                (by {{ intention.firstName }})
              </span>
            </span>
          </label>
        </div>
        
        <a 
          v-if="hasMoreIntentions" 
          @click="loadMoreIntentions" 
          class="load-more-link"
        >
          Show next 50
        </a>
      </div>
      
      <div class="buttons-container">
        <button 
          @click="continueToNextPage" 
          class="ghost-button" 
          :disabled="!selectedIntention"
        >
          Continue
        </button>
        <button 
          @click="goBack" 
          class="ghost-button"
        >
          Go Back
        </button>
      </div>
      
      <a 
        @click="signOut" 
        class="back-link"
      >
        Sign Out
      </a>
    </div>
  </div>
</template>

<script>
import { userStore } from '@/store/user'
import { API_URL } from '@/config'

export default {
  name: 'ExistingIntentionsPage',
  data() {
    return {
      searchQuery: '',
      selectedIntention: null,
      intentions: [],
      currentPage: 1,
      hasMoreIntentions: false
    }
  },
  computed: {
    user() {
      return userStore.currentUser.value
    },
    filteredIntentions() {
      return this.intentions.filter(intention => 
        intention.text.toLowerCase().includes(this.searchQuery.toLowerCase())
      )
    }
  },
  created() {
    this.fetchIntentions()
  },
  methods: {
    async fetchIntentions() {
      try {
        // Already using GET request - just needs path parameter
        const response = await fetch(`${API_URL}?path=existing-intentions&page=${this.currentPage}`)
        const data = await response.json()
        
        this.intentions = data.intentions
        this.hasMoreIntentions = data.hasMore
      } catch (error) {
        console.error('Error fetching intentions:', error)
      }
    },
    loadMoreIntentions() {
      this.currentPage++
      this.fetchIntentions()
    },
    async continueToNextPage() {
      if (this.selectedIntention) {
          try {
              // Prepare updated user data
              const updatedUser = {
                  firstName: this.user.firstName,
                  middleName: this.user.middleName,
                  lastName: this.user.lastName,
                  password: this.user.password,
                  intention: this.selectedIntention.text,
                  day: 1,
                  dateLastCompleted: this.user.dateLastCompleted || '',
                  total: this.user.total || 0,
                  petition: true,
                  thanksgiving: this.user.thanksgiving || false,
                  flameOfLove: this.user.flameOfLove !== false
              }

              // Changed to GET request with all parameters in URL
              const params = new URLSearchParams({
                  path: 'update-user',
                  ...updatedUser
              });

              const response = await fetch(`${API_URL}?${params.toString()}`, {
                  method: 'GET'
              });
              
              const data = await response.json();
              
              if (data.success) {
                  // Update user in local store
                  userStore.setUser(updatedUser)

                  // Navigate to NovenaStart
                  this.$router.push({ name: 'NovenaStart' })
              } else {
                  console.error('Failed to update user:', data)
              }
          } catch (error) {
              console.error('Error updating user:', error)
          }
      }
    },
    goBack() {
      this.$router.push({
        name: 'ChooseIntentionType'
      })
    },
    async signOut() {
      await userStore.clearUser()
      this.$router.push('/')
    }
  }
}
</script>

