<template>
    <div class="intention-container">
        <div class="content-wrapper">
            <h1 class="title">New Intention</h1>
            <div class="intention-section">
                <label class="intention-label">
                    Enter your prayer intention for this novena:
                </label>
                <textarea v-model="intention" class="intention-input" rows="4"
                    placeholder="Type your intention here..."></textarea>
                <p class="disclaimer">
                    * This intention will only be stored for you as a reminder each day you say the rosary.
                    Others will have the chance to pray for your intention, but it will not be associated
                    with your name in any way.
                </p>
            </div>
            <div class="buttons-container">
                <button @click="saveIntention" class="ghost-button" :disabled="!intention.trim()">
                    Continue
                </button>
                <button @click="goBack" class="ghost-button">
                    Go Back
                </button>
            </div>
            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
    import {
        userStore
    } from '@/store/user'
    import {
        API_URL
    } from '@/config'
    export default {
        name: 'NewIntentionPage',
        data() {
            return {
                intention: ''
            }
        },
        methods: {
            async saveIntention() {
                try {
                    const userData = {
                        firstName: this.user.firstName,
                        middleName: this.user.middleName,
                        lastName: this.user.lastName,
                        password: this.user.password,
                        intention: this.intention.trim(),
                        day: 1,
                        dateLastCompleted: this.user.dateLastCompleted || '',
                        total: this.user.total || 0,
                        petition: true,
                        thanksgiving: this.user.thanksgiving || false,
                        flameOfLove: this.user.flameOfLove !== false
                    }
                    
                    // Changed to GET request with all parameters in URL
                    const params = new URLSearchParams({
                        path: 'update-user',
                        ...userData
                    });
                    
                    const response = await fetch(`${API_URL}?${params.toString()}`, {
                        method: 'GET'
                    });
                    
                    const data = await response.json();
                    if (data.success) {
                        // Update user in local storage
                        userStore.setUser(userData)
                        // Navigate to NovenaStart page
                        this.$router.push({
                            name: 'NovenaStart'
                        })
                    }
                } catch (error) {
                    console.error('Error saving intention:', error)
                }
            },
            goBack() {
                this.$router.push({
                    name: 'ChooseIntentionType'
                })
            },
            async signOut() {
                await userStore.clearUser()
                this.$router.push('/')
            }
        },
        computed: {
            user() {
                return userStore.currentUser.value
            }
        }
    }
</script>

