import { createRouter, createWebHistory } from 'vue-router'
import { userStore } from '@/store/user'
import LoginPage from '@/views/Login.vue'
import CredentialsPage from '@/views/Credentials.vue'
import WelcomePage from '@/views/Welcome.vue'
import NovenaWarningPage from '@/views/NovenaWarning.vue'
import ChooseIntentionTypePage from '@/views/ChooseIntentionType.vue'
import NewIntentionPage from '@/views/NewIntention.vue'
import ExistingIntentionsPage from '@/views/ExistingIntentions.vue'
import NovenaStartPage from '@/views/NovenaStart.vue'
import NovenaDay from '@/views/NovenaDay.vue'
import NovenaEnd from '@/views/NovenaEnd.vue'
import Settings from '@/views/Settings.vue'
import About from '@/views/About.vue'

const routes = [
    {
        path: '/',
        name: 'LoginPage',
        component: LoginPage
    },
    {
        path: '/credentials/:mode',
        name: 'Credentials',
        component: CredentialsPage
    },
    {
        path: '/welcome',
        name: 'Welcome',
        component: WelcomePage,
        meta: { requiresAuth: true }
    },
    {
        path: '/novena-warning',
        name: 'NovenaWarning',
        component: NovenaWarningPage,
        meta: { requiresAuth: true }
    },
    {
        path: '/choose-intention-type',
        name: 'ChooseIntentionType',
        component: ChooseIntentionTypePage,
        meta: { requiresAuth: true }
    },
    {
        path: '/new-intention',
        name: 'NewIntention',
        component: NewIntentionPage,
        meta: { requiresAuth: true }
    },
    {
        path: '/existing-intentions',
        name: 'ExistingIntentions',
        component: ExistingIntentionsPage,
        meta: { requiresAuth: true }
    },
    {
        path: '/novena-start',
        name: 'NovenaStart',
        component: NovenaStartPage,
        meta: { requiresAuth: true }
    },
    {
        path: '/novena-day/:day',
        name: 'NovenaDay',
        component: NovenaDay,
        meta: { requiresAuth: true }
    },
    {
        path: '/novena-end/:day',
        name: 'NovenaEnd',
        component: NovenaEnd,
        meta: { requiresAuth: true }
    },
    {
        path: '/settings',
        name: 'Settings',
        component: Settings,
        meta: { requiresAuth: true }
    },
    {
        path: '/about',
        name: 'About',
        component: About,
        meta: { requiresAuth: true }
    }
]

const router = createRouter({
    history: createWebHistory('/rosary-novena/'),
    routes
})

router.beforeEach((to, from, next) => {
    console.log('Route guard - going to:', to.name)
    
    // Check if the route requires authentication
    if (to.matched.some(record => record.meta.requiresAuth)) {
        console.log('Route requires auth, checking authentication')
        
        // Use the new isAuthenticated method from userStore
        if (!userStore.isAuthenticated()) {
            console.log('Not authenticated, redirecting to login')
            next('/')
            return
        }
        
        console.log('Authenticated, proceeding to route')
    }
    
    next()
})

router.afterEach(() => {
    window.scrollTo(0, 0)
})

export default router