<template>
    <div class="welcome-container">
        <div class="content-wrapper" v-if="user">
            <!-- New or Inactive User -->
            <template v-if="isNewOrInactiveUser">
                <h1 class="title">Welcome {{ user.firstName }}</h1>
                <p class="status-text">You currently have no active novenas</p>
                <div class="buttons-container">
                    <button @click="startNewNovena" class="ghost-button">
                        Start New Novena
                    </button>
                </div>
            </template>

            <!-- Returning User -->
            <template v-else>
                <!-- Completed Both Petition and Thanksgiving -->
                <template v-if="hasCompletedNovena">
                    <h1 class="title">Welcome back, {{ user.firstName }}</h1>
                    <p class="status-text">
                        Congratulations on completing your rosary novena with the intention of
                        <span class="intention">{{ user.intention }}</span>
                    </p>
                    <div class="buttons-container">
                        <button @click="startNewNovena" class="ghost-button">
                            Start New Novena
                        </button>
                    </div>
                </template>

                <!-- Active Novena -->
                <template v-else-if="user.day > 0">
                    <h1 class="title">Welcome back, {{ user.firstName }}</h1>
                    <div class="status-text">
                        <p>You are on day</p>
                        <p class="day-number">{{ user.day }}</p>
                        <p>of your novena in {{ user.petition ? 'petition' : 'thanksgiving' }} for the intention of</p>
                        <p class="intention">{{ user.intention }}</p>
                    </div>
                    <div class="buttons-container">
                        <button @click="continueNovena" class="ghost-button">
                            Continue Novena
                        </button>
                        <button @click="startNewNovena" class="ghost-button">
                            Start New Novena
                        </button>
                        <button @click="goToAbout" class="ghost-button">
                            About
                        </button>
                    </div>
                </template>

                <!-- No Active Novena -->
                <template v-else>
                    <h1 class="title">Welcome back, {{ user.firstName }}</h1>
                    <p class="status-text">You currently have no active novenas</p>
                    <div class="buttons-container">
                        <button @click="startNewNovena" class="ghost-button">
                            Start New Novena
                        </button>
                    </div>
                </template>
            </template>

            <!-- Sign Out Link -->
            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
    import {
        userStore
    } from '@/store/user'
    export default {
        name: 'WelcomePage',
        data() {
            return {
                isNewUser: false
            }
        },
        computed: {
            user() {
                return userStore.currentUser.value
            },
            hasCompletedNovena() {
                return this.user && this.user.thanksgiving && this.user.day > 27
            },
            // Add this new computed property
            isNewOrInactiveUser() {
                return this.user && this.user.day === 0 && this.user.petition
            }
        },
        created() {
            // Remove checkSession call
            // The router guard ensures only authenticated users can access this page
            this.isNewUser = this.$route.params.mode === 'signup'
        },
        methods: {
            startNewNovena() {
                // If user has an active novena, show warning
                if (this.user.day > 0) {
                    this.$router.push({
                        name: 'NovenaWarning'
                    })
                } else {
                    // Otherwise go directly to choose intention type
                    this.$router.push({
                        name: 'ChooseIntentionType',
                        params: {
                            userId: `${this.user.firstName}-${this.user.lastName}`.toLowerCase()
                        }
                    })
                }
            },
            continueNovena() {
                this.$router.push({
                    name: 'NovenaStart',
                    params: {
                        userId: `${this.user.firstName}-${this.user.lastName}`.toLowerCase()
                    }
                })
            },
            goToAbout() {
                this.$router.push({ name: 'About' })
            },
            async signOut() { // methods can be async
                await userStore.clearUser()
                this.$router.push('/')
            }
        }
    }
</script>

