<template>
    <div class="choose-container">
        <div class="content-wrapper">
            <h1 class="title">Choose Prayer Intention</h1>
            <p class="choice-text">
                Would you like to pray for a new prayer intention, or search for someone else's existing prayer
                intention?
            </p>
            <div class="buttons-container">
                <button @click="goToNewIntention" class="ghost-button">
                    New Intention
                </button>
                <button @click="goToExistingIntentions" class="ghost-button">
                    Existing Intentions
                </button>
            </div>
            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
    import {
        userStore
    } from '@/store/user'
    export default {
        name: 'ChooseIntentionTypePage',
        methods: {
            goToNewIntention() {
                this.$router.push({
                    name: 'NewIntention',
                    params: {
                        userId: `${this.user.firstName}-${this.user.lastName}`.toLowerCase()
                    }
                })
            },
            goToExistingIntentions() {
                this.$router.push({
                    name: 'ExistingIntentions',
                    params: {
                        userId: `${this.user.firstName}-${this.user.lastName}`.toLowerCase()
                    }
                })
            },
            async signOut() {
                await userStore.clearUser()
                this.$router.push('/')
            }
        },
        computed: {
            user() {
                return userStore.currentUser.value
            }
        }
    }
</script>

