<template>
  <transition name="slide-up">
    <div v-if="showPrompt" class="install-banner">
      <button @click="dismiss" class="close-button"></button>
      <div class="banner-content">
        <!-- iOS Manual Instructions -->
        <div v-if="isIOS" class="ios-instructions">
          <p class="banner-text"> Install this app on your iPhone:</p>
          <p class="banner-text instruction-text">
            Tap 
            <svg width="18" height="22" fill="currentColor" viewBox="0 0 18 22" style="display: inline; vertical-align: middle; margin: 0 4px;">
              <path d="M9 0L9 15M9 0L5 4M9 0L13 4" stroke="currentColor" stroke-width="2" fill="none"/>
              <rect x="0" y="8" width="18" height="14" rx="2" fill="none" stroke="currentColor" stroke-width="2"/>
            </svg>
            <strong>Share</strong>  
            <svg width="18" height="18" fill="currentColor" viewBox="0 0 18 18" style="display: inline; vertical-align: middle; margin: 0 4px;">
              <rect x="1" y="1" width="16" height="16" rx="2" fill="none" stroke="currentColor" stroke-width="2"/>
              <line x1="9" y1="5" x2="9" y2="13" stroke="currentColor" stroke-width="2"/>
              <line x1="5" y1="9" x2="13" y2="9" stroke="currentColor" stroke-width="2"/>
            </svg>
            <strong>Add to Home Screen</strong>
          </p>
        </div>
        
        <!-- Android Install Button -->
        <div v-else>
          <p class="banner-text">Install Rosary Novena app on your device for easy access!</p>
          <div class="banner-buttons">
            <button @click="install" class="install-button">Install</button>
            <button @click="dismiss" class="dismiss-button">Not Now</button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'InstallPrompt',
  data() {
    return {
      showPrompt: false,
      deferredPrompt: null
    }
  },
  computed: {
    isIOS() {
      return /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream
    }
  },
  mounted() {
    const dismissed = localStorage.getItem('installPromptDismissed')
    const isStandalone = window.matchMedia('(display-mode: standalone)').matches || window.navigator.standalone
    const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
    
    if (!dismissed && !isStandalone && isMobile) {
      if (this.isIOS) {
        this.showPrompt = true
      } else {
        window.addEventListener('beforeinstallprompt', (e) => {
          e.preventDefault()
          this.deferredPrompt = e
          this.showPrompt = true
        })
      }
    }
  },
  methods: {
    async install() {
      if (!this.deferredPrompt) return
      
      this.deferredPrompt.prompt()
      const { outcome } = await this.deferredPrompt.userChoice
      
      if (outcome === 'accepted') {
        console.log('App installed')
      }
      
      this.deferredPrompt = null
      this.showPrompt = false
    },
    dismiss() {
      localStorage.setItem('installPromptDismissed', 'true')
      this.showPrompt = false
    }
  }
}
</script>

