<template>
    <div class="novena-day-container">
        <div class="content-wrapper">
            <!-- Mystery Image at top -->
            <img :src="currentMysteryImage" :alt="currentMystery.title" class="mystery-image">

            <h1 class="title">{{ currentMystery.title }}</h1>

            <div class="novena-details">
                <p class="day-number">Day {{ user.day }}</p>
                <p class="decade-number">{{ decadeOrdinal }} Mystery</p>
                <p class="intention" :class="{ 'thanksgiving': user.thanksgiving }">
                    {{ intentionText }}
                </p>
            </div>

            <!-- Prayer 1: Mystery Meditation (Opening) -->
            <div v-if="currentPrayer === 1" class="prayer-container">
                <audio ref="audioPlayerMysteryStart" :src="mysteryStartAudio" controls></audio>

                <p class="prayer-text">
                    {{ currentMystery.openingText }}
                </p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 2: Our Father -->
            <div v-else-if="currentPrayer === 2" class="prayer-container">
                <audio ref="audioPlayerOurFather" :src="require('@/assets/audio/our-father.mp3')" controls></audio>

                <p class="prayer-text">
                    Our Father, Who art in heaven, Hallowed be Thy Name. Thy Kingdom come. Thy Will be done, on earth as it is in Heaven. <strong>Give us this day our daily bread. And forgive us our trespasses, as we forgive those who trespass against us. And lead us not into temptation, but deliver us from evil. Amen</strong>
                </p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 3: 10 Hail Marys -->
            <div v-else-if="currentPrayer === 3" class="prayer-container">
                <audio ref="audioPlayerHailMary" :src="prayerAudio" controls></audio>

                <h5 class="hail-mary-counter">{{ hailMaryCount }} of 10</h5>
                <p class="prayer-text" style="white-space: pre-line;" v-html="prayerText"></p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 4: Glory Be -->
            <div v-else-if="currentPrayer === 4" class="prayer-container">
                <audio ref="audioPlayerGloryBe" :src="require('@/assets/audio/glory-be.mp3')" controls></audio>

                <p class="prayer-text">
                    Glory be to the Father, and to the Son, and to the Holy Spirit. <strong>As it was in the beginning, is now, and ever shall be, world without end. Amen.</strong>
                </p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 5: Fatima Prayer -->
            <div v-else-if="currentPrayer === 5" class="prayer-container">
                <audio ref="audioPlayerFatima" :src="require('@/assets/audio/fatima-prayer.mp3')" controls></audio>

                <p class="prayer-text">
                    <strong>O My Jesus, forgive us our sins, save us from the fires of Hell and lead all souls to Heaven, especially those who are in most need of Thy mercy.</strong>
                </p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Prayer 6: Mystery Meditation (Closing) -->
            <div v-else-if="currentPrayer === 6" class="prayer-container">
                <audio ref="audioPlayerMysteryEnd" :src="mysteryEndAudio" controls></audio>

                <p class="prayer-text">
                    {{ currentMystery.closingText }}
                </p>

                <img :src="rosaryImagePath" alt="Rosary Prayer" class="prayer-image">
            </div>

            <!-- Continue -->
            <div class="buttons-container">
                <button @click="continueNovena" class="ghost-button">
                    Continue
                </button>
                <button v-if="canGoBack" @click="goBack" class="ghost-button back-button">
                    Back
                </button>
            </div>

            <a @click="signOut" class="back-link">Sign Out</a>
        </div>
    </div>
</template>

<script>
import { userStore } from '@/store/user'

export default {
    name: 'NovenaDay',
    data() {
        return {
            mysteryTypes: {
                joyful: [1, 4, 7, 10, 13, 16, 19, 22, 25],
                sorrowful: [2, 5, 8, 11, 14, 17, 20, 23, 26],
                glorious: [3, 6, 9, 12, 15, 18, 21, 24, 27]
            },
            mysteriesData: {
                joyful: [
                    {
                        title: 'The Annunciation',
                        openingText: `Sweet Mother Mary, meditating on the Mystery of the Annunciation, when the angel Gabriel appeared to thee with the tidings that thou wert to become the Mother of God; greeting thee with that sublime salutation, "Hail, full of grace! The Lord is with thee!" And thou didst humbly submit thyself to the will of the Father, responding: "Behold the handmaid of the Lord. Be it done unto me according to thy word,"\n\nI humbly pray:`,
                        closingText: 'I bind these snow-white buds with a petition for the virtue of HUMILITY and humbly lay this bouquet at thy feet.',
                        virtue: 'HUMILITY'
                    },
                    {
                        title: 'The Visitation',
                        openingText: `Sweet Mother Mary, meditating on the Mystery of the Visitation, when, upon they visit to thy holy cousin, Elizabeth, she greeted thee with the prophetic utterance, "Blessed art thou among women, and blessed is the fruit of thy womb!" and thou didst answer with that canticle of canticles, the Magnificat,\n\nI humbly pray:`,
                        closingText: 'I bind these snow-white buds with a petition for the virtue of CHARITY and humbly lay this bouquet at thy feet.',
                        virtue: 'CHARITY'
                    },
                    {
                        title: 'The Nativity',
                        openingText: `Sweet Mother Mary, meditating on the Mystery of the Nativity of Our Lord, when, thy time being completed, thou didst bring forth, O holy Virgin, the Redeemer of the world in a stable at Bethlehem; whereupon choirs of angels filled the heavens with their exultant song of praise"Glory to God in the highest, and on earth peace to men of good will,"\n\nI humbly pray:`,
                        closingText: 'I bind these snow-white buds with a petition for the virtue of DETACHMENT FROM THE WORLD and humbly lay this bouquet at thy feet.',
                        virtue: 'DETACHMENT FROM THE WORLD'
                    },
                    {
                        title: 'The Presentation',
                        openingText: `Sweet Mother Mary, meditating on the Mystery of the Presentation, when, in obedience to the Law of Moses, thou didst present thy Child in the Temple, where the holy prophet Simeon, taking the Child in his arms, offered thanks to God for sparing him to look upon his Saviour and foretold thy sufferings by the words: "Thy soul also a sword shall pierce"\n\nI humbly pray:`,
                        closingText: 'I bind these snow-white buds with a petition for the virtue of PURITY and humbly lay this bouquet at thy feet.',
                        virtue: 'PURITY'
                    },
                    {
                        title: 'The Finding of the Child Jesus in the Temple',
                        openingText: `Sweet Mother Mary, meditating on the Mystery of the Finding of the Child Jesus in the Temple, when, having sought Him for three days, sorrowing, thy heart was gladdened upon finding Him in the Temple speaking to the doctors; and when, upon they request, He obediently returned home with thee,\n\nI humbly pray:`,
                        closingText: 'I bind these snow-white buds with a petition for the virtue of OBEDIENCE TO THE WILL OF GOD and humbly lay this bouquet at thy feet.',
                        virtue: 'OBEDIENCE TO THE WILL OF GOD'
                    }
                ],
                sorrowful: [
                    {
                        title: 'The Agony in the Garden',
                        openingText: `O most sorrowful Mother Mary, meditating on the Mystery of the Agony of Our Lord in the Garden, when, in the grotto of the Garden of Olives, Jesus saw the sins of the world unfolded before Him by Satan, who sought to dissuade Him from the sacrifice He was about to make; when, His soul shrinking from the sight, and His precious blood flowing from every pore at the vision of the torture and death He was to undergo, thy own sufferings, dear Mother, the future sufferings of His Church, and His own sufferings in the Blessed Sacrament, He cried in anguish, "Abba! Father! if it be possible, let this chalice pass from Me!"; but, immediately resigning Himself to His Father's will, He prayed, "Not as I will, as Thou wilt!"\n\nI humbly pray:`,
                        closingText: 'I bind these blood-red roses with a petition for the virtue of RESIGNATION TO THE WILL OF GOD and humbly lay this bouquet at thy feet.',
                        virtue: 'RESIGNATION TO THE WILL OF GOD'
                    },
                    {
                        title: 'The Scourging at the Pillar',
                        openingText: `O most sorrowful Mother Mary, meditating on the Mystery of the Scourging of our Lord, when, at Pilate's command, thy divine Son, stripped of His garments and bound to a pillar, was lacerated from head to foot with cruel scourges and His flesh torn away until His mortified body could bear no more,\n\nI humbly pray:`,
                        closingText: 'I bind these blood-red roses with a petition for the virtue of MORTIFICATION and humbly lay this bouquet at thy feet.',
                        virtue: 'MORTIFICATION'
                    },
                    {
                        title: 'The Crowning with Thorns',
                        openingText: `O most sorrowful Mother Mary, meditating on the Mystery of the Crowning of our Lord with thorns, when, the soldiers, binding about His head a crown of sharp thorns, showered blows upon it, driving the thorns deeply into His head; then, in mock adoration, knelt before Him, crying, "Hail, King of the Jews!"\n\nI humbly pray:`,
                        closingText: 'I bind these blood-red roses with a petition for the virtue of HUMILITY and humbly lay this bouquet at thy feet.',
                        virtue: 'HUMILITY'
                    },
                    {
                        title: 'The Carrying of the Cross',
                        openingText: `O most sorrowful Mother Mary, meditating on the Mystery of the Carrying of the Cross, when, with the heavy wood of the cross upon His shoulders, thy divine Son was dragged, weak and suffering, yet patient, through the streets, amidst the revilements of the people, to Calvary; falling often, but urged along by the cruel blows of His executioners,\n\nI humbly pray:`,
                        closingText: 'I bind these blood-red roses with a petition for the virtue of PATIENCE IN ADVERSITY and humbly lay this bouquet at thy feet.',
                        virtue: 'PATIENCE IN ADVERSITY'
                    },
                    {
                        title: 'The Crucifixion',
                        openingText: `O most sorrowful Mother Mary, meditating on the Mystery of the Crucifixion, when, having been stripped of His garments, thy divine Son was nailed to the cross, upon which He died after three hours of indescribable agony, during which time He begged from His Father forgiveness for His enemies,\n\nI humbly pray:`,
                        closingText: 'I bind these blood-red roses with a petition for the virtue of LOVE OF OUR ENEMIES and humbly lay this bouquet at thy feet.',
                        virtue: 'LOVE OF OUR ENEMIES'
                    }
                ],
                glorious: [
                    {
                        title: 'The Resurrection',
                        openingText: `O glorious Mother Mary, meditating on the Mystery of the Resurrection of Our Lord from the Dead, when, on the morning of the third day after His death and burial, He arose from the dead and appeared to thee, dear Mother, and filled thy heart with unspeakable joy; then appeared to the holy women, and to His disciples, who adored Him as their risen God,\n\nI humbly pray:`,
                        closingText: 'I bind these full-blown roses with a petition for the virtue of FAITH and humbly lay this bouquet at thy feet.',
                        virtue: 'FAITH'
                    },
                    {
                        title: 'The Ascension',
                        openingText: `O glorious Mother Mary, meditating on the Mystery of the Ascension, when thy divine Son, after forty days on earth, went to Mount Olivet accompanied by His disciples and thee, where all adored Him for the last time, after which He promised to remain with them until the of the world; then, extending His pierced hands over all in a last blessing, he ascended before their eyes into heaven,\n\nI humbly pray:`,
                        closingText: 'I bind these full-blown roses with a petition for the virtue of HOPE and humbly lay this bouquet at thy feet.',
                        virtue: 'HOPE'
                    },
                    {
                        title: 'The Descent of the Holy Spirit',
                        openingText: `O glorious Mother Mary, meditating on the Mystery of the Descent of the Holy Spirit, when, the apostles being assembled with thee in a house in Jerusalem, the Holy Spirit descended upon them in the form of fiery tongues, inflaming the hears of the apostles with the fire of divine love, teaching them all truths, giving to them the gift of tongues, and, filling thee with the plenitude of His grace, inspired thee to pray for the apostles and the first Christians,\n\nI humbly pray:`,
                        closingText: 'I bind these full-blown roses with a petition for the virtue of CHARITY and humbly lay this bouquet at thy feet.',
                        virtue: 'CHARITY'
                    },
                    {
                        title: 'The Assumption of Our Blessed Mother into Heaven',
                        openingText: `O glorious Mother Mary, meditating on the Mystery of Thy Assumption into Heaven, when, consumed with the desire to be united with thy divine Son in heaven, thy soul departed from thy body and united itself to Him, Who, out of the excessive love He bore for thee, His Mother, whose virginal body was His first tabernacle, took that body into heaven and there, amidst the acclaims of the angels and saints, reinfused into it thy soul.\n\nI humbly pray:`,
                        closingText: 'I bind these full-blown roses with a petition for the virtue of UNION WITH CHRIST and humbly lay this bouquet at thy feet.',
                        virtue: 'UNION WITH CHRIST'
                    },
                    {
                        title: 'The Coronation of Our Blessed Mother in Heaven as its Queen',
                        openingText: `O glorious Mother Mary, meditating on the Mystery of Thy Coronation in Heaven, when, upon being taken up to heaven after thy death, thou wert triply crowned as the august Queen of Heaven by God the Father as His beloved Daughter, by God the Son as His dearest Mother, and by God the Holy Spirit as His chosen Spouse; the most perfect adorer of the Blessed Trinity, pleading our cause as our most powerful and merciful Mother, through thee,\n\nI humbly pray:`,
                        closingText: 'I bind these full-blown roses with a petition for the virtue of UNION WITH THEE and humbly lay this bouquet at thy feet.',
                        virtue: 'UNION WITH THEE'
                    }
                ]
            },
            currentDecade: 1,
            currentPrayer: 1,
            hailMaryCount: 1,
            rosaryImageCounter: 6 // Starting from image 6
        }
    },
    computed: {
        user() {
            return userStore.currentUser.value || {}
        },
        mysteryType() {
            if (!this.user) return 'Unknown'
            const day = (this.user.day || 0)
            for (const [type, days] of Object.entries(this.mysteryTypes)) {
                if (days.includes(day)) {
                    return type.charAt(0).toUpperCase() + type.slice(1)
                }
            }
            return 'Unknown'
        },
        currentMystery() {
            const mysterySet = this.mysteriesData[this.mysteryType.toLowerCase()]
            // Add a fallback in case mysterySet is undefined
            if (!mysterySet || mysterySet.length === 0) {
                return {
                    title: '',
                    openingText: '',
                    closingText: '',
                    virtue: ''
                }
            }
            return mysterySet[this.currentDecade - 1]
        },
        currentMysteryImage() {
            try {
                const mysteryLower = this.mysteryType.toLowerCase()
                return require(`@/assets/images/${mysteryLower}/${this.currentDecade}.webp`)
            } catch (error) {
                try {
                    const mysteryLower = this.mysteryType.toLowerCase()
                    return require(`@/assets/images/${mysteryLower}/${this.currentDecade}.jpg`)
                } catch (err) {
                    console.error('Error loading mystery image:', err)
                    return ''
                }
            }
        },
        mysteryStartAudio() {
            try {
                const mysteryLower = this.mysteryType.toLowerCase()
                return require(`@/assets/audio/${mysteryLower}/${mysteryLower}-start-${this.currentDecade}.mp3`)
            } catch (error) {
                console.error('Error loading mystery start audio:', error)
                return ''
            }
        },
        mysteryEndAudio() {
            try {
                const mysteryLower = this.mysteryType.toLowerCase()
                return require(`@/assets/audio/${mysteryLower}/${mysteryLower}-end-${this.currentDecade}.mp3`)
            } catch (error) {
                console.error('Error loading mystery end audio:', error)
                return ''
            }
        },
        prayerAudio() {
            return this.user.flameOfLove ?
                require('@/assets/audio/hail-mary-flame.mp3') :
                require('@/assets/audio/hail-mary.mp3')
        },
        prayerText() {
            const baseText = 'Hail Mary, full of grace, the Lord is with Thee, blessed are Thou among women, and blessed is the fruit of Thy womb, Jesus. <strong>Holy Mary, Mother of God, pray for us sinners, '
            return this.user.flameOfLove ?
                `${baseText}spread the effect of grace of Thy Flame of Love over all of humanity, now and at the hour of our death. Amen.</strong>` :
                `${baseText}now and at the hour of our death. Amen.</strong>`
        },
        rosaryImagePath() {
            try {
                return require(`@/assets/images/rosary/${this.rosaryImageCounter}.png`)
            } catch (error) {
                console.error('Error loading rosary image:', error)
                return ''
            }
        },
        decadeOrdinal() {
            const ordinals = ['First', 'Second', 'Third', 'Fourth', 'Fifth']
            return ordinals[this.currentDecade - 1]
        },
        intentionText() {
            if (!this.user) return ''
            return this.user.thanksgiving ?
                `In thanksgiving for ${this.user.intention || ''}` :
                `In petition for ${this.user.intention || ''}`
        },
        canGoBack() {
            // Can always go back (either within NovenaDay or to NovenaStart)
            return true
        }
    },
    created() {
        if (!this.user || !this.user.day) {
            this.$router.push('/')
        }
    },
    methods: {
        continueNovena() {
            window.scrollTo(0, 0);

            // Special handling for 10 Hail Marys in prayer 3
            if (this.currentPrayer === 3 && this.hailMaryCount < 10) {
                this.hailMaryCount++
                this.rosaryImageCounter++
                
                const audioPlayer = this.$refs.audioPlayerHailMary
                if (audioPlayer) {
                    if (!audioPlayer.paused) {
                        audioPlayer.pause()
                    }
                    audioPlayer.currentTime = 0
                }
                
                return
            }
            
            // Move to next prayer
            this.currentPrayer++
            
            // Increment rosary counter when:
            // - Moving TO prayer 3 (start of Hail Marys)
            // - Moving TO prayer 4 (Glory Be, after finishing 10 Hail Marys)
            if (this.currentPrayer === 3 || this.currentPrayer === 4) {
                this.rosaryImageCounter++
            }
            
            // Reset Hail Mary counter when leaving prayer 3
            if (this.currentPrayer !== 3) {
                this.hailMaryCount = 1
            }
            
            // After completing all 6 prayers in a decade
            if (this.currentPrayer > 6) {
                if (this.currentDecade < 5) {
                    // Move to next decade without incrementing counter
                    this.currentDecade++
                    this.currentPrayer = 1
                } else {
                    this.$router.push({
                        name: 'NovenaEnd',
                        params: {
                            day: this.user.day
                        }
                    })
                }
            }
        },
        goBack() {
            window.scrollTo(0, 0);

            // Special handling for 10 Hail Marys
            if (this.currentPrayer === 3 && this.hailMaryCount > 1) {
                this.hailMaryCount--
                this.rosaryImageCounter--
                
                const audioPlayer = this.$refs.audioPlayerHailMary
                if (audioPlayer) {
                    if (!audioPlayer.paused) {
                        audioPlayer.pause()
                    }
                    audioPlayer.currentTime = 0
                }
                return
            }
            
            // Go to previous prayer
            if (this.currentPrayer > 1) {
                // Decrement when going AWAY FROM prayer 3 or prayer 4
                if (this.currentPrayer === 3 || this.currentPrayer === 4) {
                    this.rosaryImageCounter--
                }
                
                this.currentPrayer--
                
                // If going back to prayer 3, set hailMaryCount to 10
                if (this.currentPrayer === 3) {
                    this.hailMaryCount = 10
                }
            } else if (this.currentDecade > 1) {
                this.currentDecade--
                this.currentPrayer = 6
            } else {
                this.$router.push({
                    name: 'NovenaStart'
                })
            }
        },
        async signOut() {
            await userStore.clearUser()
            this.$router.push('/')
        }
    }
}
</script>

