<template>
  <div class="menu-bar" v-if="user">
    <div class="left-section">
      <p class="user-name">
        <i>{{ formatName }}</i>
      </p>
      <p class="stats">
        Rosaries: {{ user.total || 0 }} | Novenas: {{ user.totalNovenas || 0 }}
      </p>
    </div>
    <div class="right-section" v-if="isWelcomePage">
      <a @click="openSettings" class="settings-link">Settings</a>
    </div>
  </div>
</template>

<script>
import { userStore } from '@/store/user'

export default {
  name: 'MenuBar',
  computed: {
    user() {
      return userStore.currentUser.value
    },
    formatName() {
      if (!this.user) return ''
      
      const middleInitial = this.user.middleName ? 
        `${this.user.middleName.charAt(0)}.` : ''
      
      return `${this.user.firstName} ${middleInitial}`
    },
    isWelcomePage() {
      return this.$route.name === 'Welcome'
    }
  },
  methods: {
    openSettings() {
      this.$router.push({ name: 'Settings' })
    }
  }
}
</script>

