<template>
  <div class="credentials-container">
    <div class="content-wrapper">
      <h1 class="title">{{ showForgotPassword ? 'Forgot Password' : 'Enter Credentials' }}</h1>
      
      <!-- Forgot Password Form -->
      <form v-if="showForgotPassword" @submit.prevent="handleForgotPassword" class="credentials-form">
        <div class="input-group">
          <label for="forgotFirstName">First Name</label>
          <input type="text" id="forgotFirstName" v-model="forgotCredentials.firstName" required>
        </div>
        <div class="input-group">
          <label for="forgotMiddleName">Middle Name</label>
          <input type="text" id="forgotMiddleName" v-model="forgotCredentials.middleName" required>
        </div>
        <div class="input-group">
          <label for="forgotLastName">Last Name</label>
          <input type="text" id="forgotLastName" v-model="forgotCredentials.lastName" required>
        </div>
        <div class="input-group">
          <label for="securityAnswer">Security Question: Who is your favorite Saint?</label>
          <input type="text" id="securityAnswer" v-model="forgotCredentials.securityAnswer" required>
        </div>
        
        <div v-if="isLoading" class="message">
          <div class="spinner"></div>
          <p class="loading-text">Checking...</p>
        </div>
        <p v-else-if="message" :class="['message', messageType]">{{ message }}</p>

        <button type="submit" class="ghost-button" :disabled="isLoading">
          Retrieve Password
        </button>
        <a @click="cancelForgotPassword" class="back-link" :class="{ 'disabled': isLoading }">Cancel</a>
      </form>

      <!-- Regular Login/Signup Form -->
      <form v-else @submit.prevent="handleSubmit" class="credentials-form">
        <div class="input-group">
          <label for="firstName">First Name</label>
          <input type="text" id="firstName" v-model="credentials.firstName" required>
        </div>
        <div class="input-group">
          <label for="middleName">Middle Name</label>
          <input type="text" id="middleName" v-model="credentials.middleName" required>
        </div>
        <div class="input-group">
          <label for="lastName">Last Name</label>
          <input type="text" id="lastName" v-model="credentials.lastName" required>
        </div>
        <div class="input-group">
          <label for="password">Password</label>
          <input :type="mode === 'signup' ? 'text' : 'password'" id="password" v-model="credentials.password" required>
        </div>
        
        <!-- Security Question (only for signup) -->
        <div v-if="mode === 'signup'" class="input-group">
          <label for="securityQuestion">Security Question: Who is your favorite Saint?</label>
          <input type="text" id="securityQuestion" v-model="credentials.securityQuestion" required>
        </div>

        <div v-if="isLoading" class="message">
          <div class="spinner"></div>
          <p class="loading-text">Checking credentials...</p>
        </div>
        <p v-else-if="message" :class="['message', messageType]">{{ message }}</p>

        <button type="submit" class="ghost-button" :disabled="isLoading">
          {{ mode === 'signup' ? 'Create New Account' : 'Sign In' }}
        </button>
        
        <!-- Forgot Password Link (only for signin) -->
        <a v-if="mode === 'signin'" @click="showForgotPasswordForm" class="forgot-password-link">
          Forgot Password?
        </a>
        
        <a @click="goBack" class="back-link" :class="{ 'disabled': isLoading }">Go Back</a>
      </form>
    </div>
  </div>
</template>

<script>
  import {
    API_URL
  } from '@/config'
  import {
    userStore
  } from '@/store/user'
  export default {
    name: 'CredentialsPage',
    data() {
      return {
        mode: '',
        credentials: {
          firstName: '',
          middleName: '',
          lastName: '',
          password: '',
          securityQuestion: ''
        },
        forgotCredentials: {
          firstName: '',
          middleName: '',
          lastName: '',
          securityAnswer: ''
        },
        message: '',
        messageType: '',
        isLoading: false,
        showForgotPassword: false
      }
    },
    created() {
      this.mode = this.$route.params.mode
    },
    methods: {
      showForgotPasswordForm() {
        this.showForgotPassword = true
        this.message = ''
        this.messageType = ''
      },
      
      cancelForgotPassword() {
        this.showForgotPassword = false
        this.forgotCredentials = {
          firstName: '',
          middleName: '',
          lastName: '',
          securityAnswer: ''
        }
        this.message = ''
        this.messageType = ''
      },

      async handleForgotPassword() {
        this.isLoading = true
        try {
          const params = new URLSearchParams({
            path: 'forgot-password',
            firstName: this.forgotCredentials.firstName.trim(),
            middleName: this.forgotCredentials.middleName.trim(),
            lastName: this.forgotCredentials.lastName.trim(),
            securityQuestion: this.forgotCredentials.securityAnswer.trim()
          })

          const response = await fetch(`${API_URL}?${params.toString()}`)
          const data = await response.json()

          if (data.success) {
            this.message = `Your password is: ${data.password}`
            this.messageType = 'success'
          } else {
            this.message = data.message || 'Could not retrieve password. Please check your information.'
            this.messageType = 'error'
          }
        } catch (error) {
          console.error('Forgot password error:', error)
          this.message = 'Error retrieving password. Please try again.'
          this.messageType = 'error'
        } finally {
          this.isLoading = false
        }
      },

      async handleSubmit() {
        const user = {
          firstName: this.credentials.firstName.trim(),
          middleName: this.credentials.middleName.trim(),
          lastName: this.credentials.lastName.trim(),
          password: this.credentials.password.trim(),
          securityQuestion: this.credentials.securityQuestion.trim()
        }
        if (this.mode === 'signin') {
          await this.checkExistingUser(user)
        } else {
          await this.createNewUser(user)
        }
      },
      async checkExistingUser(user) {
        this.isLoading = true;
        try {
          console.log('Attempting login with:', user);
          const params = new URLSearchParams({
            path: 'check-user',
            firstName: user.firstName,
            middleName: user.middleName,
            lastName: user.lastName,
            password: user.password
          });
          
          const response = await fetch(`${API_URL}?${params.toString()}`, {
            method: 'GET'
          });
          
          const data = await response.json();
          if (data.success) {
            userStore.setUser(data.user);
            await this.$router.push({
              name: 'Welcome'
            });
          } else {
            this.message = data.message || 'Login failed';
            this.messageType = 'error';
          }
        } catch (error) {
          console.error('Login error:', error);
          this.message = 'Error checking credentials';
          this.messageType = 'error';
        } finally {
          this.isLoading = false;
        }
      },
      async createNewUser(user) {
        this.isLoading = true;
        try {
          const newUser = {
            firstName: user.firstName,
            middleName: user.middleName,
            lastName: user.lastName,
            password: user.password,
            securityQuestion: user.securityQuestion,
            intention: "",
            day: 0,
            dateLastCompleted: "",
            total: 0,
            totalNovenas: 0,
            petition: true,
            thanksgiving: false,
            flameOfLove: true
          }
          
          const params = new URLSearchParams({
            path: 'create-user',
            ...newUser
          });
          
          const response = await fetch(`${API_URL}?${params.toString()}`, {
            method: 'GET'
          });
          
          const data = await response.json();
          if (data.success) {
            userStore.setUser(data.user);
            await this.$router.push({
              name: 'Welcome'
            });
            this.message = 'Account created! Redirecting...';
            this.messageType = 'success';
          } else {
            this.message = data.message || 'Error creating account. Please try again.';
            this.messageType = 'error';
          }
        } catch (error) {
          console.error('Error:', error);
          this.message = 'Error creating account. Please try again.';
          this.messageType = 'error';
        } finally {
          this.isLoading = false;
        }
      },
      goBack() {
        this.$router.push('/')
      }
    }
  }
</script>

<style scoped>
  .credentials-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }

  .content-wrapper {
    width: 100%;
    max-width: 400px;
  }

  .title {
    font-family: 'Libre Baskerville', serif;
    color: #707070;
    font-weight: 300;
    font-size: 2rem;
    letter-spacing: 0.05em;
    margin-bottom: 40px;
    text-align: center;
  }

  .credentials-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .input-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
    text-align: left;
  }

  .input-group label {
    font-family: 'Libre Baskerville', serif;
    color: #707070;
    font-size: 16px;
  }

  .input-group input {
    padding: 10px;
    border: 1px solid #707070;
    font-family: 'Libre Baskerville', serif;
    font-size: 16px;
  }

  .ghost-button {
    font-family: 'Libre Baskerville', serif;
    color: #707070;
    background: transparent;
    border: 1px solid #707070;
    padding: 12px 24px;
    font-size: 18px;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 100%;
    margin-top: 20px;
  }

  .ghost-button:hover {
    background-color: rgba(112, 112, 112, 0.1);
  }

  .message {
    text-align: center;
    padding: 10px;
    margin: 0;
  }

  .success {
    color: #4CAF50;
  }

  .error {
    color: #f44336;
  }

  .forgot-password-link {
    display: block;
    text-align: center;
    color: #707070;
    text-decoration: underline;
    cursor: pointer;
    font-family: 'Libre Baskerville', serif;
    transition: color 0.3s ease;
    font-size: 14px;
  }

  .forgot-password-link:hover {
    color: #4a4a4a;
  }

  .back-link {
    display: block;
    text-align: center;
    margin-top: 20px;
    color: #707070;
    text-decoration: underline;
    cursor: pointer;
    font-family: 'Libre Baskerville', serif;
    transition: color 0.3s ease;
  }

  .back-link:hover {
    color: #4a4a4a;
  }

  .spinner {
    width: 24px;
    height: 24px;
    border: 3px solid #70707040;
    border-top: 3px solid #707070;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin: 0 auto;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  .loading-text {
    color: #707070;
    text-align: center;
    font-family: 'Libre Baskerville', serif;
    margin-top: 8px;
  }

  .ghost-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }

  .back-link.disabled {
    opacity: 0.7;
    cursor: not-allowed;
    pointer-events: none;
  }
</style>